import React, { useState, useEffect } from 'react'
import Web3 from 'web3'
import { useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Container, Grid, makeStyles, Button } from '@material-ui/core'

import Loading from '../../components/Loading'

import { listToken, getAuction } from '../../services/Web3/token'

import {
  selectArtworkByTokenId,
  fetchAllArtworks,
  fetchArtwork,
  syncAuction
} from '../../store/artwork/artworkSlice'

import Clip from '../../components/Clip'
import { isVideo } from '../../utils'

import { IMG_BASE } from '../../constant/'



const List = (props) => {
  const dispatch = useDispatch()
  let history = useHistory()

  let { tokenId } = props.match.params

  //remove
  tokenId = tokenId.toString()

  const userSlice = useSelector((state) => state.user)
  //  const artworkSlice = useSelector((state) => state.artwork)
  const artwork = useSelector((state) => selectArtworkByTokenId(state, tokenId))

  const [formData, setFormData] = useState({
    price: 0,
  })

  const [buttonState, setButtonState] = useState({
    listing: false,
  })
  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })


  useEffect(() => {
    if (!artwork) {
      //get the awrwork from server
      dispatch(fetchArtwork(tokenId))
    }
  }, [artwork])


  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
  }

  const handleListNFT = async () => {
    try {
      console.log('listing')
      setButtonState({ ...buttonState, listing: true })

      let artworkAuction = await getAuction(tokenId)
      if (artworkAuction.active) {
        dispatch(syncAuction())
        throw 'Already Listed in Auction'
      }

      let ListedNFT = await listToken(tokenId, formData.price)

      dispatch(fetchAllArtworks())

      setButtonState({ ...buttonState, listing: false })
      history.push(`/${artwork.username}/${artwork.url}`)

      // console.log(savedArtwork)
    } catch (err) {
      setButtonState({ ...buttonState, listing: false })
      console.log(err)
    }
  }



  if (  !artwork ||  !userSlice.user ||   artwork.creator !== userSlice.user.publicAddress) {
    return <Loading />
  }

  return (
    <div className="root mint">
      <Container>
        <div className="content">
          <div>
            <div className="title">List your Artwork</div>
          </div>

          <div className="row-2">
            <div className="mint-sideContainer">
              <div className="artworkInput-container">
                <label htmlFor="artworkInput">
                  {isVideo(artwork.artwork) ? (
                    <Clip url={IMG_BASE + artwork.artwork} className="video" />
                  ) : (
                    <img className="img" src={IMG_BASE + artwork.artwork}></img>
                  )}
                </label>
              </div>
            </div>

            <div className="mint-sideContainer">
              {dailog.active ? (
                <div className="dailog-containter">
                  <div className={dailog.status + '-alert alert'}>
                    {dailog.message}
                  </div>
                </div>
              ) : (
                <div></div>
              )}

              <div className="form-control-o">
                <label>Price</label>
                <input
                  required
                  name="price"
                  placeholder="price"
                  value={formData.price}
                  onChange={handleChange}
                />
              </div>

              <div className="form-control-o">
                {buttonState.listing ? (
                  <button>Listing.. Please wait!</button>
                ) : (
                  <button onClick={handleListNFT}> List your NFT </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </Container>
    </div>
  )
}
export default List
