artworkAPI.syncAuction = async (data) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/artwork/sync/auction`, {
                headers: {
                    'Content-Type': 'application/json',
                },
                method: 'POST',
                body: JSON.stringify(data),
            })
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    }
    )
}

artworkAPI.getArtworkTransaction = async ({tokenId}) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/artwork/get/transactions/`+tokenId, {
                headers: {
                    'Content-Type': 'application/json',
                },
                method: 'GET',
            })
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}