import React, { useState, useEffect } from 'react'
import ipfs from '../../services/ipfs'
import {  useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Container} from '@material-ui/core'

//import Loading from '../../components/Loading'
import Clip from '../../components/Clip'

//import { web3, connectMetaMask } from '../../services/web3'
import { mintToken } from '../../services/Web3/token'

import { fetchAllArtworks } from '../../store/artwork/artworkSlice'
import { isVideo } from '../../utils'

import './index.css'
import NoImage from '../../images/No-Image.jpg'

const Mint = (props) => {
  const dispatch = useDispatch()
  let history = useHistory()

  const userSlice = useSelector((state) => state.user)

  const [progress, setProgress] = useState("idle")

  const [imgData, setImgData] = useState({
    fileType: 'image',
    imgData: '',
    imgUrl: NoImage,
  })

  const [formData, setFormData] = useState({
    title: '',
    description: '',
    artwork: '',
    metadata: '',
  })

  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: ''
  })

  useEffect(() => {
    if (userSlice.status == 'succeeded' && userSlice.user.username == '') {
      history.push('/profile/edit')
    }
  }, [userSlice])

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
    setDailog({
      active: false,
      status: '',
      message: ''
    })
  }

  const handleImgUpload = async (e) => {
    e.stopPropagation()
    e.preventDefault()
    try {
      const file = e.target.files[0]
      const name = e.target.files[0].name
      let fileType = 'image'

      let imgUrl = await readImg(file, 'read')
      let imgData = await readImg(file, 'data')

      const imgBuffer = await Buffer.from(imgData)

      if (isVideo(name)) {
        fileType = 'video'
      }

      setImgData({
        imgUrl: imgUrl,
        imgData: imgBuffer,
        fileType: fileType,
      })
      setProgress("file")
    } catch (err) {
      console.log(err)
    }
  }

  const readImg = (file, type) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      if (type == 'data') {
        reader.readAsArrayBuffer(file)
      } else {
        reader.readAsDataURL(file)
      }
      reader.onloadend = async () => {
        resolve(reader.result)
      }
    })
  }

  const uploadArtwork = async () => {
    try {
      console.log('uploading nft img')

      if (progress === "uploading") {
        return false
      }
      setProgress("uploading")
      setDailog({
        active: false,
        status: '',
        message: ''
      })

      let path = '/nft.jpg'
      if (imgData.fileType == 'video') {
        path = '/nft.mp4'
      }

      let ipfsImgResponse = await ipfs.add(
        {
          path: path,
          content: imgData.imgData,
        },
        { wrapWithDirectory: true },
      )


      let artworkHash = ipfsImgResponse[1].hash
      artworkHash = artworkHash + path

      setFormData({...formData, artwork: artworkHash })

      setDailog({
        active: true,
        status: 'success',
        message: "uploaded artwork to ipfs successfully"
      })
      setProgress("uploaded")

      // const uploadedArtwork = await createArtwork(userSlice.accessToken, {
      //   artworkHash,
      // })
      //   console.log(uploadedArtwork)
    } catch (err) {
      console.log(err)
      setFormData({ ...formData, artwork: '' })
      setProgress("file")
      setDailog({
        active: true,
        status: 'error',
        message: err.message
      })
    }
  }

  const handleMintNFT = async (e) => {
    e.preventDefault()
    try {
      console.log('minting')

      if(formData.title === "" || formData.description === "" || formData.artwork === ""){
        throw {message: "Fill the Form"} 
      }

      setProgress("minting")

      const metaData = JSON.stringify({
        title: formData.title,
        description: formData.description,
        artwork: formData.artwork,
      })

      let ipfsmetaDataResponse = await ipfs.add(Buffer.from(metaData))
      let metadataHash = ipfsmetaDataResponse[0].hash

      let mintedNFT = await mintToken(metadataHash, formData.artwork)

      // fetch the artwork again in reduers
      dispatch(fetchAllArtworks())

      setProgress("uploaded")

      history.push(
        '/creator/list/' + mintedNFT.events.Mint.returnValues.tokenId,
      )
    } catch (err) {
      setProgress("uploaded")
      setDailog({
        active: true,
        status: 'error',
        message: err.message
      })
    }
  }

  const RenderButton = () => {
    if (progress === "uploaded")  {
      return <button className="uploadBtn">Uploaded to IPFS</button>
    }else  if (progress === "uploading")  {
      return <button className="uploadBtn">Uploading.. Please wait!</button>
    } else  if (progress === "file")  {
      return ( <button className="uploadBtn" onClick={uploadArtwork}> Upload to IPFS</button>
      )
    } else {
      return (
        <input
          className="uploadFileBtn"
          name="artwork"
          id="artworkInput"
          type="file"
          onChange={handleImgUpload}
        ></input>
      )
    }
  }

  return (
    <div className="root mint">
      <Container>
        <div className="content">
          <div>
            <div className="title">Mint your Artwork</div>
          </div>

          <div className="row-2">
            <div className="mint-sideContainer">
              <div className="artworkInput-container">
                <label htmlFor="artworkInput">
                  {imgData.fileType == 'video' ? (
                    <Clip url={imgData.imgUrl} className="video" />
                  ) : (
                    <img className="img" src={imgData.imgUrl}></img>
                  )}
                </label>
              </div>

              <div style={{ display: 'block' }}>{RenderButton()}</div>
            </div>

            <div className="mint-sideContainer">
                  
                  {dailog.active? (
                    <div className="dailog-containter">
                    <div className={dailog.status+ "-alert alert"}>{dailog.message}</div>
                  </div>
                  ): (
                    <div></div>
                  )}
                

              <div className="form-control hide">
                <label>Artwork Hash</label>
                <input
                  required
                  readOnly
                  name="artwork"
                  value={formData.artwork}
                  onChange={handleChange}
                />
              </div>
              <div className="form-control-o">
                <label>Title</label>
                <input
                  required
                  name="title"
                  placeholder="Title"
                  value={formData.title}
                  onChange={handleChange}
                />
              </div>

              <div className="form-control-o">
                <label>description</label>
                <input
                  required
                  name="description"
                  placeholder="Description"
                  value={formData.description}
                  onChange={handleChange}
                />
              </div>

              <div className="form-control-o">
                {progress === "minting" ? (
                  <button>MINTING.. Please wait!</button>
                ) : (
                  <button
                    className={progress === "uploaded" ? 'show' : 'hide'}
                    onClick={handleMintNFT}
                  >
                    MINT NFT
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </Container>
    </div>
  )
}
export default Mint
