import React, {  } from 'react'
import { NavLink as RouterLink } from 'react-router-dom'
import { useSelector } from 'react-redux'
import MetaMask from '../../MetaMask'

import Logo from '../../../images/logo/icon/logo_icon_color.png'

import Navbar from '../../Navbar/Navbar'
import SearchBar from '../../SearchBar'
import UserButton from '../UserButton'

const Desktop = ({ openMetamaskModel,  closeMetaMaskModal, showMetaMaskModal}) => {
  const userSlice = useSelector((state) => state.user)

  return (
    <div>
      <Navbar>
        <div>
          <RouterLink className="logo-container" exact to="/">
            <img className="logo" src={Logo} />
          </RouterLink>
        </div>

        <SearchBar />

        <div className="nav-row">
          <div className="sub-navbar">
            <div className="nav-item">
              <div className="item">
                <RouterLink exact activeClassName="active" to="/">
                  Explore
                </RouterLink>
              </div>
            </div>
            <div className="nav-item">
              <div className="item">
                <RouterLink exact activeClassName="active" to="/artworks">
                  Artworks
                </RouterLink>
              </div>
            </div>
            <div className="nav-item">
              <div className="item">
                <RouterLink exact activeClassName="active" to="/creators">
                  Creators
                </RouterLink>
              </div>
            </div>
            <div className="nav-item">
              <div className="item">
              {userSlice.user? (
              <RouterLink exact activeClassName="active" to={`/${userSlice.user.publicAddress}`}>My items</RouterLink> 
              ): (
                <a className="link-btn" onClick={openMetamaskModel} >My items</a> 
                )} 
           
                {/* {userSlice.user? '': onClick={openMetamaskModel}} */}
              </div> 
            </div>
           
          </div>

          <div className="divider-vertical"></div>

          <div className="sub-navbar">
            <div className="nav-item">
              <div className="item">
                <RouterLink exact activeClassName="active" to="/faq">
                  How it works
                </RouterLink>
              </div>
            </div>
            {/* <div className="nav-item">
              <div className="item">
                <RouterLink exact activeClassName="active" to="/community">
                  Community
                </RouterLink>
              </div>
            </div> */}
          </div>
        </div>

        {/* 
        <nav>
          <ul>
            <li>
              <RouterLink exact activeClassName="active" to="/artworks">
                Artwork
              </RouterLink>
            </li>

            <li>
              <RouterLink exact activeClassName="active" to="/">
                Home
              </RouterLink>
            </li>
            <li>
              <RouterLink exact activeClassName="active" to="/creators">
                Creators
              </RouterLink>
            </li>
          </ul>
        </nav> */}


        {userSlice.user ? (
          <UserButton user={userSlice.user}></UserButton>
        ) : (
          <div>
            <button onClick={openMetamaskModel} className="connect-button">
              Connect Wallet
            </button>
          </div>
        )}


      </Navbar>

      <MetaMask
        showModal={showMetaMaskModal}
        handleClose={closeMetaMaskModal}
      />
    </div>
  )
}

export default Desktop
