import React from 'react'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import {
  Avatar,
  CardActionArea,
  Grid,
  CardContent,
  CardMedia,
  Typography,
} from '@material-ui/core'

import Countdown from '../../components/Countdown'
import Clip from '../../components/Clip'

import { selectCreatorById } from '../../store/creator/creatorSlice'

import { IMG_BASE } from '../../constant'
import { isVideo } from '../../utils'

import EmptyAvatar from '../../images/avatar.jpg'

import './index.css'

const ArtworkCard = (props) => {
  const {
    url,
    title,
    artwork,
    price,
    description,
    username,
    owner,
    isAuction,
    endTime,
    creator,
  } = props.artwork

  const RenderMedia = (artwork, title) => {
    if (isVideo(artwork)) {
      return (
        <div className="media">
          <Clip url={IMG_BASE + artwork} className="video" />
        </div>
      )
    } else {
      return (
        <div className="media">
          <img className="img" src={IMG_BASE + artwork} title={title} />
        </div>
      )
    }
  }

  const RenderDescription = (title, creator) => {
    return (
      <div className="content">
        <div className="title">
          <p>{title}</p>
        </div>
        {RenderCreator(creator)}
      </div>
    )
  }

  const RenderCreator = (creatorId) => {
    const creator = useSelector((state) => selectCreatorById(state, creatorId))

    const avatar = creator && creator.avatar ? creator.avatar : EmptyAvatar
    const username = creator && creator.username ? creator.username : 'unknown'
    return (
      <div className="creator-container">
        <Avatar className="creator-avatar" src={IMG_BASE + avatar} />
        <p className="creator-name"> {'@' + username} </p>
      </div>
    )
  }


  const renderTimmer = () => {
    if (isAuction) {
      let dif = parseInt(endTime) - parseInt(Math.floor(Date.now() / 1000))
        return (
          <div>
            <div className="faded-text">Ending in</div>
            {dif > 0? (<Countdown type="mini-countdown" endTime={endTime} />) : (<div className="bold-text">Auction has Ended</div>) }
          </div>
        )
      
    }
  }

  const RenderAutionDetails = () => {
    let value = price / 1e18

    return (
      <div className="aution-container" >
        <div className="aution-details" >
          <div className="faded-text">{isAuction ? 'Current Bid' : 'Sold for'}</div>
          <div className="bold-text">{value} ETH </div>
        </div>
        {isAuction? renderTimmer(): (<div></div>)}
        
      </div>
    )
  }

  return (
    <Link
      to={`/${username ? username : creator}/${url}`}
      className="card card-artwork"
    >
      {RenderMedia(artwork, title)}

      {RenderDescription(title, creator)}

        {RenderAutionDetails()}
     
    </Link>
  )
}

export default ArtworkCard
