import React, { useState, useRef, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { CSSTransition } from 'react-transition-group'
import { ReactComponent as EtherIcon } from '../../images/svg/ether.svg'
import { ReactComponent as CopyIcon } from '../../images/svg/copy.svg'

import './index.css'

const Dropdown = ({ username, publicAddress, handleLogOut,balance }) => {
  const [menuHeight, setMenuHeight] = useState(null)
  const dropdownRef = useRef(null)

  useEffect(() => {
    setMenuHeight(dropdownRef.current?.firstChild.offsetHeight)
  }, [])

  function calcHeight(el) {
    const height = el.offsetHeight
    setMenuHeight(height)
  }

  const subKey = `${publicAddress.slice(0, 4)}...${publicAddress.slice(
    publicAddress.length - 4,
    publicAddress.length,
  )}`
  const id = username? username: publicAddress

  return (
    <div className="dropdown" style={{ height: menuHeight }} ref={dropdownRef}>
      <CSSTransition
        in={true}
        timeout={500}
        classNames="menu-primary"
        unmountOnExit
        onEnter={calcHeight}
      >
        <div className="menu">
          <div className="header">
            <div className="user">
              <div className="username">
                <Link to={`/${id}`}>{ username? username: subKey}</Link>
                <span>
                  <CopyIcon />
                </span>
              </div>
            </div>
          </div>
          <div className="wallet">
            <div className="icon">
              <EtherIcon />
            </div>
            <div className="wallet-content">
              <div className="title">Balance</div>
              <div className="balance">
                <span>{parseFloat(balance).toFixed(4)}</span> <span>ETH</span>{' '}
              </div>
            </div>
          </div>
          <div className="divider"> </div>

          <div className="nav-list">
            <div className="item"><Link to={`/${id}`}>My items</Link></div>
            <div className="item"><Link to={`/bids`}>My Bids</Link></div>
            <div className="item"><Link to={'/profile/edit'}>Edit profile</Link></div>
            <div onClick={handleLogOut} className="item">Disconnect</div>
          </div>
        </div>
      </CSSTransition>
    </div>
  )
}

export default Dropdown
