import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector  } from 'react-redux'

import Clip from '../../components/Clip'
import Loading from '../../components/Loading'

import { isVideo } from '../../utils'

import {
  selectArtworkById,
  fetchArtwork
} from '../../store/artwork/artworkSlice'

import { IMG_BASE } from '../../constant'

import ArtworkDetails from './ArtworkDetails'
import AuctionDetails from './AuctionDetails'
import ArtworkHistory from './ArtworkHistory'

import './artwork.css'

const Artwork = (props) => {

  const dispatch = useDispatch()

  const { creator: creatorUsername, artwork: artworkId } = props.match.params
  const artwork = useSelector((state) => selectArtworkById(state, artworkId))
  const error = useSelector((state) => state.artwork.error)

  //const [artwork, setArtwork] = useState(selectedArtwork)

  if (error) {
    console.log('ERROR: ' + error)
  }

  useEffect(()=>{
    if(!artwork){
      dispatch(fetchArtwork(artworkId))
    }

  },[artwork])

  if (!artwork) {
    return <Loading/>
  }

  return (
    <div className="root artwork-page">
      <div className="cover">
        <div className="media">
          {isVideo(artwork.artwork) ? (
            <Clip className="video" url={IMG_BASE + artwork.artwork} />
          ) : (
            <img className="img" src={IMG_BASE + artwork.artwork} />
          )}
        </div>
      </div>

      <div className="content">
        <div className="row-2">
          <div className="right-container">
            <ArtworkDetails artwork={artwork} />
          </div>

          <div className="left-container">
          <div className="insdie-left-container">
              <AuctionDetails artwork={artwork} />
              <ArtworkHistory artwork={artwork} />
            </div>
          </div>
        </div>
      </div>
  
    </div>
  )
}
export default Artwork
