import React from 'react'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import {isVideo} from '../../utils'
import Clip from '../../components/Clip'


import { IMG_BASE } from '../../constant'
import './index.css'
import CoverImg from '../../images/cover.jpg'
import AvatarImg from '../../images/avatar.jpg'

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    marginTop: 20,
  },

  cardRoot: {
    display: 'flex',
    flexDirection: 'column',
    boxShadow: '0px 10px 20px rgba(0, 0, 0, 0.05)',
    borderRadius: '10px',
    overflow: 'hidden',
    transition: 'all 300ms cubic-bezier(0.23, 1, 0.32, 1)',
    textDecoration: 'none',
    willChange: 'transform',
    color: '#000000',
    '&:hover': {
      transform: ' translateY(-4px)',
      boxShadow: 'rgb(0 0 0 / 10 %) 0px 10px 20px',
    },
  },
  cardHead: {
    color: '#000000',
    position: 'relative',
  },
  cardCover: {
    color: '#000000',
    position: 'relative',
    overflow: 'hidden',
  },
  cardCoverHolder: {
    color: '#000000',
    width: '100%',
    height: '0',
    paddingBottom: '57.142857142857146%',
  },
  cardCoverContainer: {
    position: 'absolute',
    top: '0',
    right: '0',
    bottom: '0',
    left: '0',
    backgroundColor: '#F2F2F2',
    display: 'flex',
  },
  cardCoverImg: {
    color: '#000',
    maxWidth: '100%',
    height: 'auto',
    display: 'block',
    objectFit: 'cover',
    minHeight: '100%',
    minWidth: '100%',
  },
  cardCoverAvatar: {
    color: '#000',
    marginLeft: '24px',
    marginRight: '24px',
    position: 'relative',
  },
  cardCoverAvatarContainer: {
    boxSizing: 'border-box',
    color: '#000',
    padding: '8px',
    backgroundColor: '#FFFFFF',
    width: '96px',
    height: '96px',
    position: 'absolute',
    left: '0',
    transform: 'translateY(-50%)',
    display: 'flex',
    borderRadius: '999px',
  },
  cardCoverAvatarImg: {
    minWidth: '80px',
    minHeight: '80px',
    maxWidth: '80px',
    maxHeight: '80px',
    backgroundImage:
      'linear-gradient(135deg , rgb(82, 63, 239), rgb(253, 34, 173))',
    backgroundRepeat: 'initial',
    backgroundAttachment: 'initial',
    backgroundOrigin: 'initial',
    backgroundClip: 'initial',
    backgroundColor: '#F2F2F2',
    backgroundSize: 'cover',
    backgroundPosition: 'center center',
    borderRadius: '999px',
  },
  cardBody: {
    paddingLeft: '24px',
    paddingRight: '24px',
    paddingTop: '64px',
    paddingBottom: '48px',
    border: '1px solid #ffffff36',
    borderTop: 'none'
  },
  cardBodyContainer: {
    display: 'grid',
    gridGap: '8px',
    marginBottom: '16px',
  },
  cardBodyTitle: {
    letterSpacing: '-0.03em',
    fontSize: '32px',
    fontWeight: '600',
  },
  cardBodyCreator: {
    color: '#000',
    display: 'flex',
  },
  cardBodyCreatorContainer: {
    color: '#000',
    fontSize: '18px',
    fontWeight: '600',
  },
  cardBodyCreatorLink: {
    color: '#000',
    fontSize: '18px',
    fontWeight: '600',
    background:
      'linear-gradient(110.78deg, #76E650 -1.13%, #F9D649 15.22%, #F08E35 32.09%, #EC5157 48.96%, #FF18BD 67.94%, #1A4BFF 85.34%, #62D8F9 99.57%)',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
    letterSpacing: '-0.01em',
  },
  cardBodyDescription: {
    color: '#000',
    fontWeight: '400',
    lineHeight: '1.7',
    fontSize: '15px',
  },
})

const CreatorCard = (props) => {
  const classes = useStyles()

  let {
    _id,
    fullname,
    username,
    bio,
    coverImg,
    avatar,
    publicAddress,
  } = props.creator

  return (
    <Link
      to={`/${username ? username : publicAddress}`}
      className="card card-creator"
    >
      <div className={classes.cardHead}>
        <div className={classes.cardCover}>
          <div className={classes.cardCoverHolder}></div>
          <div className={classes.cardCoverContainer}>
            {isVideo(coverImg) ? (
                <Clip url={IMG_BASE + coverImg} className="video"  />
              ) : (
                <img className={classes.cardCoverImg} src={coverImg? IMG_BASE + coverImg: CoverImg} className="img" />
              )}

          </div>
        </div>
        <div className={classes.cardCoverAvatar}>
          <div className={classes.cardCoverAvatarContainer}>
            <div
              className={classes.cardCoverAvatarImg}
              style={{
                backgroundImage: `url(${avatar? IMG_BASE+ avatar : AvatarImg})`,
              }}
            ></div>
          </div>
        </div>
      </div>
      <div className={classes.cardBody}>
        <div className={classes.cardBodyContainer}>
          <div className={classes.cardBodyTitle}>{fullname}</div>
          <div className={classes.cardBodyCreator}>
            <div className={classes.cardBodyCreatorContainer}>
              <div className={classes.cardBodyCreatorLink}>@{username}</div>
            </div>
          </div>
        </div>
        <div className={classes.cardBodyDescription}>{bio}</div>
      </div>
    </Link>
  )
}

export default CreatorCard
