import jwtDecode from 'jwt-decode';


export const fetchUser = async (accessToken) => {
    return new Promise(async (resolve, reject) => {
        try {

            const { payload: { id } } = jwtDecode(accessToken);
            let response = await fetch(`/api/users/${id}`, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                },
            })
            resolve(await response.json())
            return
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')
            
        }
    })
}

export const patchUser = async (accessToken, data) => {
    return new Promise(async (resolve, reject) => {
        try {
            let response = await fetch(`/api/users/profile/edit`, {
                headers: {
                    'Authorization': `Bearer ${accessToken}`,
                    'Content-Type': 'application/json',
                },
                method: 'PATCH',
                body: JSON.stringify(data),
            })
            
          //  console.log("response")
          //  console.log(response)
            if (response.ok) {
                return resolve(await response.json())
            }
            throw new Error(response.statusText)
            
        } catch (err) {
            console.log(err)
            return reject(err.message ? err.message : 'internal error')

        }
    })
}

