import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'

import { claimNft } from '../../../services/Web3/token'

const AuctionFooter = ({ artwork, artworkAuction }) => {
  const user = useSelector((state) => state.user.user)

  let endTime = parseInt(artworkAuction.endTime)
  let currentTime = parseInt(Math.floor(Date.now() / 1000))
  let dif = endTime - currentTime

  const handleNFTClaim = async (event) => {
    event.preventDefault()
    try {
      if (!artwork.tokenId || artwork.tokenId == undefined) {
        throw 'token id not found'
      }
      const tokenId = artwork.tokenId.toString()
      let mintedNFT = await claimNft(tokenId)
      console.log(mintedNFT)
    } catch (err) {
      console.log(err)
    }
  }

  const footerLink = (url, title) => {
    return (
      <div className="footer">
        <Link to={url} className="footer-link">
          <button className="bg-btn">{title}</button>
        </Link>
      </div>
    )
  }

  if (user && artwork.creator == user.publicAddress) {
    if (artwork.status == 'Mint') {
      return footerLink('/creator/list/' + artwork.tokenId, 'List in Auction')
    } else if (dif <= 0) {
      if (artwork.status === 'Auction') {
        return (
          <div className="footer">
            <button className="bg-btn" onClick={handleNFTClaim}>Settle NFT</button>
          </div>
        )
      }
    }

    return <div></div>
  }

  if (dif <= 0) {
    if (user && artwork.owner == user.publicAddress) {
      return footerLink(`/bids`, 'Claim Your NFT')
    } else {
      return <div></div>
    }
  }
  if (!artworkAuction.active) {
    return <div></div>
  }

  return footerLink(`/${artwork.username}/${artwork.url}/bid`, 'Place a bid')
}

export default AuctionFooter
