import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import {
  selectLiveArtworks,
  selectSoldArtworks,
} from '../../store/artwork/artworkSlice'

import ArtworkCard from '../../components/Card/ArtworkCard'

// import { artworkAPI } from '../../api/artwork'

import './artworks.css'

const Artworks = () => {
  const liveArtworks = useSelector((state) => selectLiveArtworks(state))
  const soldArtworks = useSelector((state) => selectSoldArtworks(state))

  const [activeTab, setActiveTab] = useState('live')

  const handleTab = (e) => {
    e.preventDefault()
    let tab = e.target.dataset.value
    setActiveTab(tab)
  }



  const renderArtworks = () => {
    // switch(activeTab){
    //   case 'sold':
    //   break;
    //   case 'reserve':
    //   break;
    // }

    if (activeTab == 'sold') {
      if (soldArtworks >= 0) {
        return (
          <div className="no-artworks">
            <div>No Artworks</div>
          </div>
        )
      }
      return soldArtworks.map((artwork) => {
        return (
          <div className="item" key={artwork._id}>
            <ArtworkCard artwork={artwork} />
          </div>
        )
      })
    } else if (activeTab == 'reserve') {

      // /
      // /
      // /
      // /
   

    } else {
      if (soldArtworks >= 0) {
        return (
          <div className="no-artworks">
            <div>No Artworks</div>
          </div>
        )
      }
      return liveArtworks.map((artwork) => {
        return (
          <div className="item" key={artwork._id}>
            <ArtworkCard artwork={artwork} />
          </div>
        )
      })
    }
  }

  return (
    <div className="root artworks-page container">
      <div className="tabs-container">
        <div className="tabs">
          <div className={activeTab === 'live' ? 'item active' : 'item'}>
            <div onClick={handleTab} data-value="live" className="text">
              Live auction
            </div>
          </div>
          {/* <div
            value="reserve"
            className={activeTab === 'reserve' ? 'item active' : 'item'}
          >
            <div onClick={handleTab} data-value="reserve" className="text">
              Reserve not met
            </div>
          </div> */}
          <div
            value="sold"
            className={activeTab === 'sold' ? 'item active' : 'item'}
          >
            <div onClick={handleTab} data-value="sold" className="text">
              Sold
            </div>
          </div>
        </div>
      </div>

      <div className="artworks-grid">{renderArtworks()}</div>
    </div>
  )
}
export default Artworks
