import React from 'react'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { selectMyBids } from '../../store/artwork/artworkSlice'
import { claimNft } from '../../services/Web3/token'


import ArtworkCard from '../../components/Card/ArtworkCard'

import './index.css'

const Bids = () => {
  const user = useSelector((state) => state.user.user)
  const myBids = useSelector((state) => selectMyBids(state))
  let currentTime = parseInt(Math.floor(Date.now() / 1000))

  const handleNFTClaim = async (event) => {
    event.preventDefault()

    try {
      const tokenId = event.target.value.toString()
      let mintedNFT = await claimNft(tokenId)
    } catch (err) {
      console.log(err)
    }
  }

  if (!user) {
    return ''
  }

  const BiddedArtwork = ({ bid }) => {
    return (
      <div className="biddedArtwork">
        <ArtworkCard artwork={bid} />
        <div className="status">I'm {bid.status}</div>
        <div className="bidding-action">
          {currentTime > parseInt(bid.endTime) ? (
            <button className="bidding-btn" value={bid.tokenId} onClick={handleNFTClaim}>
              Collect
            </button>
          ) : (
            ''
          )}
          {bid.status == 'Outbid' ? (
            <Link className="bidding-btn" to={`${bid.username}/${bid.url}/bid`}> Bid Again</Link>
          ) : (
            <Link className="bidding-btn" to={`${bid.username}/${bid.url}`}> View </Link>
          )}
        </div>
      </div>
    )
  }

  return (
    <div className="myBids-page">
      <div>
        <div className="title">My Bids</div>
      </div>

      <div className="bidding-container">
        {myBids.map((bid) => {
          return <BiddedArtwork key={bid._id} bid={bid} />
        })}
      </div>
    </div>
  )
}

export default Bids
