import './index.css'

const FAQ = () => {
  return (
    <div className="faq-page">
      <div className="container">
        <div>
          <h1>MarketPlace</h1>
          <div className="q-con">
            <div className="q">NFT? ERC-721 tokens?</div>
            <div className="a">
              NFT stands for non-fungible tokens like ERC-721 (a smart contract
              standard) tokens which are hosted on Ethereums own blockchain.
              NFTs are unique digital items such as collectibles or artworks or
              game items. As an artist, by tokenizing your work you both ensure
              that it is unique and brand it as your work. The actual ownership
              is blockchain-managed. If you want to go in-depth into NFTs, I
              suggest this read:{' '}
              <a>[https://opensea.io/blog/guides/non-fungible-tokens/]</a>
              <a>(https://opensea.io/blog/guides/non-fungible-tokens/)</a>
            </div>
          </div>

          <div className="q-con">
            <div className="q">What does minting mean?</div>
            <div className="a">The process of tokenizing your work and creating an NFT (see above).
            </div>
          </div>

          <div className="q-con">
            <div className="q">Can I create an NFT on digiplaza.com without putting it on sale?</div>
            <div className="a">Yes, you can and it is up to you if you decide to sell it later or not.
            </div>
          </div>

          <div className="q-con">
            <div className="q"> Can I change the price of an already created collectible?</div>
            <div className="a"> Absolutely, you can lower the price free of transaction costs at any time. You just need to sign the signature request via your wallet.
            </div>
          </div>
          <div className="q-con">
            <div className="q">Can I gift or send a collectible to someone? </div>
            <div className="a"> Yes, just go on the detail page of a collectible you own, open the ... menu and select transfer token
            </div>
          </div>
          <div className="q-con">
            <div className="q">What does burn a token mean? </div>
            <div className="a"> The ERC-721 standard does not only allow the creation of NFTs, but also includes a possibility to destroy them - i.e. burning the token.
            </div>
          </div>
       

          <h1>Governance</h1>


          <div className="q-con">
            <div className="q"> What is Digiplaza governance system? How does it work? </div>
            <div className="a"> The ultimate goal of Rarible is to evolve into a fully Decentralized Autonomous Organization (DAO), where all governance and decision rights belong to the platform users. By providing creators and collectors with the opportunity to propose and vote on platform upgrades, we make sure that the platform becomes a public good, responsive directly to its community members. On Rarible, you have a voice, and your voice is heard.
            </div>
          </div>


          <div className="q-con">
            <div className="q">Im a Digiplaza user. Can I share my idea/proposal? </div>
            <div className="a"> We encourage self expression on any matter: whether you have an idea or a concern, if you want to support a project or disagree with others  share, express, and push it. Every opinion is counted, heard and valued. You can learn more about your rights here.

            </div>
          </div>


          <div className="q-con">
            <div className="q"> </div>
            <div className="a"> 
            </div>
          </div>



        </div>
      </div>
    </div>
  )
}

export default FAQ
