import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import Loading from '../../components/Loading'
import { placeBet } from '../../services/Web3/token'
import { getCurrentBalance } from '../../services/Web3/web3'
import {
  selectArtworkById,
  syncAuction,
} from '../../store/artwork/artworkSlice'
import { selectCreatorById } from '../../store/creator/creatorSlice'

import ArtworkCard from '../../components/Card/ArtworkCard'

import './index.css'

const Bid = (props) => {
  const dispatch = useDispatch()
  const history = useHistory()

  const { creator: creatorUsername, artwork: artworkId } = props.match.params
  const artwork = useSelector((state) => selectArtworkById(state, artworkId))
  const creator = useSelector((state) =>
    selectCreatorById(state, creatorUsername),
  )
  const userSlice = useSelector((state) => state.user)

  const [formData, setFormData] = useState({
    token: 0,
    value: 0,
    leastBid: 0,
  })

  const [balance, setBalance] = useState({
    ether: 0,
  })

  const [buttonState, setButtonState] = useState({
    bid: false,
  })

  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
    setDailog({
      active: false,
      status: '',
      message: ''
    })
  }

  useEffect(() => {
    if (userSlice.user) {
      ;(async () => {
        try {
          let balance = await getCurrentBalance()

          setBalance({ ...balance, ether: balance })

          if (artwork) {
            let price = parseFloat(artwork.price)
            price = price + (price * 5) / 100
            price = price / 1e18
            price = parseFloat(price).toFixed(6)

            setFormData({ ...formData, leastBid: price })
          }
        } catch (err) {
          console.log(err)
        }
      })()
    }
  }, [userSlice.user, artwork])

  const handlePlaceBid = async (e) => {
    e.preventDefault()
    try {
      if (!userSlice.user) {
        return 
      }
      setButtonState({ ...buttonState, bid: true })
      let value = parseFloat(formData.value)
      if (value <= 0 && value < formData.leastBid) {
        setDailog({
          active: true,
          status: 'error',
          message: 'value must be greater than the least bid'
        })
        setButtonState({ ...buttonState, bid: false })
        return 
      }

      let bidedTx = await placeBet(artwork.tokenId, value)

      dispatch(syncAuction({ tokenId: artwork.tokenId }))
     // setButtonState({ ...buttonState, bid: false })
      history.push('/bids')

    } catch (err) {
      console.log(err)
      setDailog({
        active: true,
        status: 'error',
        message: 'invalid value'
      })
      setButtonState({ ...buttonState, bid: false })

      return 
    }
  }

  if (!artwork) {
    // wait for artowrk slice and if its not in the slice then request for it in database
    return <Loading />
  }

  return (
    <div className="content">
      <div style={{ padding: '20px' }}></div>
      <div className="row-2">
        <div>
          <ArtworkCard artwork={artwork} />
        </div>

        {userSlice.user ? (
          <div className="bid-container">
            <div className="title">Place a bid</div>
            <div className="fadded-sub-title">You must bid at least</div>
            <div className="bold-value">{formData.leastBid} ETH</div>
            <div>
              {dailog.active ? (
                <div className="dailog-containter">
                  <div className={dailog.status + '-alert alert'}>
                    {dailog.message}
                  </div>
                </div>
              ) : (
                <div></div>
              )}

              <div className="form-control-o">
                <input
                  name="value"
                  value={formData.value}
                  placeholder="Value"
                  onChange={handleChange}
                />
              </div>

              {buttonState.bid ? (
                <button className="btn btn-bidding">
                  Bidding.. Please wait
                </button>
              ) : (
                <button className="btn btn-bidding" onClick={handlePlaceBid}>
                  Place the Bid
                </button>
              )}
            </div>
          </div>
        ) : (
          <div className="bid-container">
            <div className="title">Connect to MetaMask</div>
          </div>
        )}
      </div>
    </div>
  )
}
export default Bid
