import React from 'react'
import { IMG_BASE, TOKEN_URI } from '../../constant'

import { ReactComponent as EtherscanIcon }  from '../../images/svg/etherscan.svg'
import { ReactComponent as ExitIcon }  from '../../images/svg/exit.svg'
import { ReactComponent as IPFSIcon }  from '../../images/svg/ipfs.svg'
import { ReactComponent as ArrowDownIcon }  from '../../images/svg/arrow-down.svg'


const ArtworkDetails = ({artwork})=> {



    return(
        <div className="artwork-details-container">
        <div>
          <div  className="title">  {artwork.title}   </div> 
          <div className="artwork-info">
          <ArrowDownIcon/>
          <div>Artwork information</div>
        </div>
          </div>

          <div className="artwork-details">
          <div>
          <div className="sub-header">Description</div>
          <div>{artwork.description}</div>
        </div>

        <div>
          <div className="sub-header">Edition of</div>
          <div className="text-big">1</div>
        </div>



        <div className="external-links">

          <a className="external-link" href={TOKEN_URI + artwork.tokenId} target="_blank" rel="noreferrer">
            <EtherscanIcon/>
            <div className="text">View on Etherscan</div>
            <div className="exit"><ExitIcon/></div>
          </a>

          <a className="external-link"  href={IMG_BASE + artwork.metadata} target="_blank" rel="noreferrer">
            <IPFSIcon/>
            <div className="text">View on IPFS</div>
            <div className="exit"><ExitIcon/></div>
          </a>

        </div>
          </div>

       
       
      </div>

    )
}

export default ArtworkDetails