import { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {Link} from 'react-router-dom'
import moment from 'moment'
import { selectCreatorById, fetchUser } from '../../store/creator/creatorSlice'

import {isVideo} from '../../utils/file'

import Clip from '../../components/Clip'

import { ReactComponent as ExitIcon } from '../../images/svg/exit.svg'

import { IMG_BASE,ETHERSCAN_URL } from '../../constant'

import './index.css'

const Transction = ({ transaction }) => {

  const dispatch = useDispatch()

    let user = transaction.username? transaction.username : (transaction.from).toLowerCase()

    if(transaction.type === "ClaimNft"){
      user = (transaction.to).toLowerCase()
    }

    const creator = useSelector((state) => selectCreatorById(state, user))

    const creatorStatus = useSelector((state) =>state.creator.status)
    useEffect(()=>{
      if(!creator){
        if(creatorStatus !== "loading"){
          dispatch(fetchUser(user))
        }
      }
    },[])

 

 
    //has to select this user
    let description = ""
    let avatar = creator && creator.avatar? creator.avatar:  ""
    let username = creator && creator.username? creator.username: user
    if(transaction.type == "Mint"){

    }
  
    return (
      <div className="tranasction">
        <Link to={"/"+user} className="avatar-image">
      { isVideo(avatar)? (<div className="media">
          <Clip url={IMG_BASE + avatar} className="video" />
        </div>): (<div className="media">
          <img className="img" src={IMG_BASE + avatar} title={user} />
        </div>) 
        }
        </Link>
        <div className="description">
          <div>
            Artwork {transaction.type} by <span className="creator-username">@{username}</span>
          </div>
          <div>{moment(transaction.date).format('LLL')}</div>
        </div>
        <div className="price">
          {transaction.type == 'Bid' ? (
            <div>
              <div>{(parseInt(transaction.value) / 1e18).toFixed(4)} ETH</div>
              {/* <div>${(parseInt(transaction.value) / 1e18).toFixed(4)}</div> */}
            </div>
          ) : (
           <div></div>
          )}
          <div className="action">
          <a className="external-link"  href={ETHERSCAN_URL+ '/tx/'+transaction.hash} target="_blank" rel="noreferrer">
          <ExitIcon />
          </a>
        

      </div>
        </div>
        
      </div>
    )
  }

  export default Transction