import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import Web3 from 'web3'
import { creatorAPI } from '../../api/creator'
const initialState = {
  creators: [],
  error: null,
  status: 'idle',
}

export const fetchAllCreators = createAsyncThunk(
  'creator/fetchAllCreators',
  async () => {
    //fetch user from here
    let creators = await creatorAPI.fetchAllCreators()
    return creators
  },
)
export const fetchUser = createAsyncThunk(
  'creator/fetchUser',
  async (username) => {
    //fetch user from here
    let creator = await creatorAPI.fetchUser(username)
    return creator
  },
)

const creatorSlice = createSlice({
  name: 'creator',
  initialState,
  reducers: {},
  extraReducers: {
    [fetchAllCreators.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchAllCreators.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      state.creators = action.payload
    },
    [fetchAllCreators.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
    [fetchUser.pending]: (state, action) => {
      state.status = 'loading'
    },
    [fetchUser.fulfilled]: (state, action) => {
      state.status = 'succeeded'
      state.creators = [...state.creators, action.payload]
    },
    [fetchUser.rejected]: (state, action) => {
      state.status = 'failed'
      state.error = action.error.message
    },
  },
})

export default creatorSlice.reducer

export const selectCreatorById = (state, creatorId) => {
  let isAddress = Web3.utils.isAddress(creatorId)
  if (isAddress) {
    return state.creator.creators.find(
      (creator) => creator.publicAddress === creatorId,
    )
  } else {
    return state.creator.creators.find(
      (creator) => creator.username === creatorId,
    )
  }
}
export const selectCreators = (state) => {
  return state.creator.creators.filter((creator) => creator.role === 'creator')
}
