import React, { useState, useEffect } from 'react'

import Transction from '../../components/Transaction'

import { artworkAPI } from '../../api/artwork'



const ArtworkHistory = ({ artwork }) => {
  const { getArtworkTransaction } = artworkAPI

  const [transactions, setTransactions] = useState([])
  useEffect(() => {
    console.log('artwork use effect ')
    if (artwork) {
      ;(async () => {
        try {
          const transactions = await getArtworkTransaction({
            tokenId: artwork.tokenId,
          })
          setTransactions(transactions)
        } catch (err) {
          console.log(err)
        }
      })()
    }
  }, [artwork])



  return (
    <div>
      <div style={{fontSize: '24px',  fontWeight: '600', margin: '0px 0px 24px'}}>
      History 
      </div>
      <div className="artwork-history-list">
      {transactions.map((transaction) => {
        return <Transction key={transaction._id} transaction={transaction} />
    
      })}
      </div>

    </div>
  )
}

export default ArtworkHistory
