import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Container, InputBase } from '@material-ui/core'
import { fade, makeStyles } from '@material-ui/core/styles'
import SearchIcon from '@material-ui/icons/Search'
import CreatorCard from '../../components/Card/CreatorCard'
import { Grid } from '@material-ui/core'

// import { fetchAllCreators } from '../../store/creator/creatorSlice'

const useStyles = makeStyles((theme) => ({
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    marginTop: 30,
    marginBottom: '32px',
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
    borderRadius: '999px',
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    padding: theme.spacing(2, 1, 2, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    transition: 'box-shadow 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s',
    boxShadow:
      ' rgb(230 230 230) 0px 0px 0px 1px inset, rgb(0 0 0 / 5%) 0px 10px 20px',
    borderRadius: '999px',
    fontSize: '18px',
  },
}))

const Creators = (props) => {
  const classes = useStyles()

  const dispatch = useDispatch()

  //const creatorSlice = useSelector((state) => state.creator)
  const creators = useSelector((state) => state.creator.creators)

  useEffect(() => {
    // console.log(creatorSlice.status)
    // if (creatorSlice.status === 'idle') {
    //   console.log("run")
    //   dispatch(fetchAllCreators())
    // }
  }, [])

  return (
    <Container>
      <div className={classes.search}>
        <div className={classes.searchIcon}>
          <SearchIcon />
        </div>
        <InputBase
          placeholder="Search for a creator"
          classes={{
            root: classes.inputRoot,
            input: classes.inputInput,
          }}
          inputProps={{ 'aria-label': 'Search for a creator' }}
        />
      </div>

      <div>
        <Grid
          direction="row"
          justify="flex-start"
          alignItems="center"
          justify="center"
          container
          className={classes.root}
          spacing={2}
        >
          <Grid item xs={12}>
            <Grid container spacing={3}>
              {creators &&
                creators.map((creator) => {
                  if (creator.username != '') {
                    return (
                      <Grid key={creator._id} item md={3}>
                        <CreatorCard creator={creator} />
                      </Grid>
                    )
                  }
                })}
            </Grid>
          </Grid>
        </Grid>
      </div>
    </Container>
  )
}
export default Creators
