import React, {useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Backdrop } from '@material-ui/core'

import Dropdown from '../../Drowndown/index'
import { logOut } from '../../../store/user/userSlice'
import { getCurrentBalance } from '../../../services/Web3/web3'

import './index.css'
import { IMG_BASE } from '../../../constant'

const UserButton = ({ user }) => {

    const dispatch = useDispatch()

    const userSlice = useSelector((state) => state.user)

    const [open, setOpen] = useState(false)
    const [balance, setBalance] = useState(0)

    useEffect(() => {
        if (userSlice.user) {
          ;(async () => {
            let balance = await getCurrentBalance()
            balance = parseFloat(balance)
            balance = balance.toFixed(4)
            setBalance(balance)
          })()
        }
      }, [userSlice.user])

    const handleLogOut = () => {
        dispatch(logOut())
      }



    const { publicAddress, username, avatar } = user
  
    let avatarImg = IMG_BASE + avatar

    return (
      <div className="right-nav">

        <Link to="/creator/create" className="create-btn">
          Create
        </Link>

        <div className="user-container">

          <button onClick={() => setOpen(!open)} className="user-btn">
            <div className="user-btn-content">
              <h6>{parseFloat(balance).toFixed(2)} ETH</h6>
            </div>
            <div className="userAvatar">
              <div className="userAvatarContainer">
                  <img src={avatarImg} />
              </div>
            </div>
          </button>

          {open && (
            <Dropdown
              balance={balance}
              username={username}
              publicAddress={publicAddress}
              handleLogOut={handleLogOut}
            />
          )}
          {/* {open && (
            <Backdrop open={open} onClick={() => setOpen(!open)}></Backdrop>
          )} */}
        </div>
      </div>
    )
  }


  export default UserButton